.currentPage {
    font-weight: bold;
}

.page {
    padding: 1rem;
}

.pageButtons {
    margin: 1rem auto;
    max-width: 70vw;
    height: 2rem;
    white-space: nowrap;
    cursor: pointer;
    overflow-x: scroll;
    overflow-y: hidden;

}

@media (min-width: 0) {
    .usersGrid{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 10px;
    }
}
@media (min-width: 800px) {
    .usersGrid{
        padding: 10px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}


.userWrapper {
    background-color: #eeeeee;
    border-radius: 60px;
    margin: 10px;
    padding: 10px;
    min-width: 40px;
    transition-duration: .3s;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 100px 1fr;
    grid-template-rows: 100px;
    grid-template-areas: "pp inf";
    font-family: "Arial", sans-serif;
    text-decoration: none;
    line-height: 100px;
    vertical-align: middle;
    color: black;
}

.userWrapper:hover {
    transform: translateX(4px);
    transition-duration: .2s;
}

.pPicture {
    grid-area: pp;
}

.pPicture img {
    height: 100px;
    object-fit: cover; /* Do not scale the image */
    object-position: center; /* Center the image within the element */
    width: 100px;
    border-radius: 50px;
}


.info {
    grid-area: inf;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.name {
    font-family: "Arial", sans-serif;
    font-weight: bold;
}

.status {
    font-family: "Arial", sans-serif;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #555555;
}
/* width */
::-webkit-scrollbar {
    width: 10px;
    height: 6px;

}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 6px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 6px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}